<script setup lang="ts">
    import { GpnButton, GpnDialog } from "gpn-ui";
    import { useGlobalStore } from "../stores/store";
    import { ref } from "vue";

    const store = useGlobalStore();

    const dialogShow = ref(false);
    const dialogElem = ref<InstanceType<typeof GpnDialog> | null>(null);

    function openDialog() {
        dialogElem.value?.show();
    }
</script>

<template>
    <h2>Dialog</h2>
    <div class="grid">
        <GpnButton :theme="store.theme" label="Open" @click="dialogShow = true" />
        <GpnButton :theme="store.theme" label="Open from code" @click="openDialog" />

        <GpnDialog v-model="dialogShow" :theme="store.theme" title="Заголовок модального окна №1">
            <template #buttons="{ ok, cancel }">
                <GpnButton size="l" label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                <GpnButton size="l" label="Отмена" view="secondary" :theme="store.theme" @click="cancel('cancel val')" />
            </template>
        </GpnDialog>

        <GpnDialog ref="dialogElem" :theme="store.theme" title="Заголовок модального окна №2">
            <div style="width: 100%">Some dialog text</div>
            <template #buttons="{ ok, cancel }">
                <GpnButton size="l" label="Сохранить" :theme="store.theme" @click="ok('some val')" />
                <GpnButton size="l" label="Отмена" view="secondary" :theme="store.theme" @click="cancel('cancel val')" />
            </template>
        </GpnDialog>
    </div>
</template>

